.swipe-list{
    height: 100%;
    /* position: relative; */
    overflow: hidden;
    box-sizing: border-box;
}

/* 수정 예정 */
.swipe-item {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-size: cover;
  
    animation: slider 50s infinite ;
  }
  
  .swipe-item:nth-child(1) {
    background-image: url(../../asset/swipe/swipe01.jpg);
    animation-delay: 0;
  }
  .swipe-item:nth-child(2) {
    background-image: url(../../asset/swipe/swipe02.JPG);
    animation-delay: -10s;
  }
  .swipe-item:nth-child(3) {
    background-image: url(../../asset/swipe/swipe03.jpg);
    animation-delay: -20s;
  }
  .swipe-item:nth-child(4) {
    background-image: url(../../asset/swipe/swipe04.jpg);
    animation-delay: -30s;
  }
  .swipe-item:nth-child(5) {
    background-image: url(../../asset/swipe/swipe05.jpg);
    animation-delay: -40s;
  }

  @keyframes slider {
    0% {
      transform: translateX(0);
    }
    10% {
      transform: translateX(0);
    }
    20% {
      transform: translateX(-100%);
      animation-timing-function: step-end;
    }
    90% {
      transform: translateX(100%);
    }
  
    100% {
      transform: translateX(0);
    }
    
  }