@import url('https://fonts.googleapis.com/css2?family=42dot+Sans:wght@300..800&family=Black+Han+Sans&family=IBM+Plex+Sans+KR&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

.box {
  /* margin: 15px; */
  margin-top: 50px;
  width: 100%;
  text-align: center;
}

.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 5px;
  /* margin: 4rem 2vw; */
  padding: 0;
  list-style-type: none;
}

.card {
  display: block;
  cursor: pointer;
}

.card_active {
  display: block;
  cursor: pointer;

  position: relative;
  animation: fadeInUp linear;
  animation-timeline: view();
}

.card > div {
  display: grid;
  /* gap: 5px; */
}
.card_title_wrapper{
  /* align-items: to; */
}

.card__image {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.card_title {
  font-size: 1rem;
  margin: -2px 0 0.3em;
  color: #a09f9f;
  padding: 0 0 15px 0;
  text-align: left;
  align-items: start;
  font-weight: bold;
}

@media (max-width:450px){
  .box {
    margin: 5px;
    margin-top: 40px;
    text-align: center;
  }
  .cards {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
  .card{
    padding: 0 15px;
  }
  .card__image {
    height: 30px;
    width: 30px;
  }

}

@keyframes fadeInUp {
  0% {
      opacity: 0;
      transform: translate3d(0, 100%, 0);
  }
  to {
      opacity: 1;
      transform: translateZ(0);
  }
}
