@import url('https://fonts.googleapis.com/css2?family=42dot+Sans:wght@300..800&family=Black+Han+Sans&family=IBM+Plex+Sans+KR&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

.Experience{
  display: grid;
  /* justify-content: center; */
  /* align-items: center; */
  /* text-align: center; */
  /* min-height: 100vh; */

  font-family: var(--default-font);
}

.Experience-wrapper{
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 5% 0;
  box-sizing: border-box;
}

.heading{
  margin-bottom: 40px;
  font-size: 2rem;
}

.cards{
  display: flex;
  gap: 30px;
  font-size: 1.5rem;
}

.cards-item > div{
  width: 400px;
  height: 300px;
  border-radius: 10px;
  overflow: hidden;

}
.cards-item:hover{
  cursor: pointer;
}
.img{
  object-fit: cover;
  width: 100%;
  height: 100%;
}