@import url('https://fonts.googleapis.com/css2?family=42dot+Sans:wght@300..800&family=Black+Han+Sans&family=IBM+Plex+Sans+KR&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

.Detail{
  display: grid;
  box-sizing: border-box;
  font-family: var(--default-font);
}

.Detail > div {
  padding: 10px;
  box-sizing: border-box;
}

.Detail > div > img {
  width: 100%;
}

.desc{
  display: flex;
  justify-content: space-between;
  padding: 0 30px ! important;
  box-sizing: border-box;
}

.desc > div > h1 {
  font-weight: 600;
  text-transform: uppercase;
}

.etc{
  text-align: end;
  font-weight: 500;
}

.icons{
  display: flex;
  gap: 25px;
}

.icons > div > img{
  height: 100px;
}

.images{
  display: grid;
  gap: 20px;
  
}
.images > div {
  display: flex;
  width: 100%;
  justify-content: space-between;
}
.images > div > img {
  width: 100%;
}