@import url('https://fonts.googleapis.com/css2?family=42dot+Sans:wght@300..800&family=Black+Han+Sans&family=IBM+Plex+Sans+KR&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

.header{
  width: 100%;
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;

  padding: 20px;
  box-sizing: border-box;

  font-family: var(--default-font);
  font-size: 2rem;
  font-weight: 600;

  /* position: fixed; */
  position: relative;
  z-index: 1;
}

.header > div:hover{
  cursor: pointer;
}

.header > ul {
  list-style: none;
  display: flex;
  gap: 40px;
}

.header > ul > li{
    cursor: pointer;
}

.active{
  color: #8c8c8c ! important;
}